.compare-bikes {
   &__products {
      @include page-grid;

      row-gap: 40px;
   }

   &__product {
      grid-column: 1 / -1;

      @include on-tablet {
         grid-column: 2 / -2;
      }

      @include on-desktop {
         grid-column: span 4;
      }
   }
}

.product {
  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    text-align: left;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    text-align: left;
  }

  &__price {
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    text-align: left;
  }

  @include hover(transform, scale(1.1));
}
